import { negativeSignKey } from './debit-credit-sign';
import { Types, TimePeriod, BillingStatus } from './constants';
import getCategoryIconClass from './get-category-icon-class';

const helpers = (context) => {
  const i18nFilter = context.$filter('i18n');

  return {
    /**
     * @name transactionTypes
     * @type {Types}
     *
     * @description
     * Transaction types constant
     */
    transactionTypes: Types,

    /**
     * @name getSignedAmount
     * @type {function}
     * @param {object} transaction Transaction object
     * @returns {number} Signed amount
     *
     * @description
     * Based on credit/debit indicator, put right sign on the transaction amount
     */
    getSignedAmount: (transaction) =>
      transaction.amount * (transaction.creditDebitIndicator === negativeSignKey ? -1 : 1),

    /**
     * @name isPaginationTypeMatch
     * @type {function}
     * @param {function} $ctrl      Current controller
     * @param {string} type         Description of pagination type (pagination, load-more)
     * @returns {boolean}
     *
     * @description
     * Checkes if actual pagination type matches the one, defined in properties
     */
    isPaginationTypeMatch: ($ctrl, type) =>
      $ctrl.state.pageParams.paginationType === type,

    /**
     * @name showNoTransactions
     * @type {function}
     * @param {function} $ctrl      Current controller
     * @returns {boolean}
     *
     * @description
     * Checks if conditions are met to show the no transactions icon and message
     */
    showNoTransactions: $ctrl =>
      $ctrl.transactions
      && !$ctrl.loading
      && !$ctrl.searching
      && !Object.keys($ctrl.transactions).length,

    /**
     * @name showNoResults
     * @type {function}
     * @param {function} $ctrl      Current controller
     * @returns {boolean}
     *
     * @description
     * Checks if conditions are met to show the no result icon and message
     */
    showNoResults: ($ctrl) => {
      // should return true if it transactions or searchTransactions are empty
      if (!$ctrl.searchTransactions && !$ctrl.transactions) {
        return true;
      } else if (
        $ctrl.searching &&
        !$ctrl.loading &&
        $ctrl.searchTransactions &&
        Object.keys($ctrl.searchTransactions).length === 0
      ) {
        return true;
      } else if (
        !$ctrl.searching &&
        !$ctrl.loading &&
        $ctrl.transactions &&
        Object.keys($ctrl.transactions).length === 0
      ) {
        return true;
      }

      return false;
    },
    /**
     * @name getCategoryIconClass
     * @type {function}
     * @param {string} transactionCategory Transaction category
     * @param {?boolean} withPrefix Include class prefix
     * @returns {string}
     *
     * @description
     * Converts transaction category name into category CSS icon class
     */
    getCategoryIconClass,

    /**
     * @name dateLabel
     * @type {function}
     * @param {string} label date
     * @returns {string}
     *
     * @description
     * Returns either label like now, today or yesterday or the actual date
     */
    dateLabel: (label) => i18nFilter(TimePeriod[label]) || label,

    /**
     * @name billingStatusLabel
     * @type {function}
     * @param {string} label enum billing
     * @returns {string}
     *
     * @description
     * Returns label for billing status like pending or unbilled
     */
    billingStatusLabel: (label) => i18nFilter(BillingStatus[label]) || label,
  };
};

export default helpers;



